import {mapState} from 'vuex';
import f from "@/application/functions";
import _ from 'underscore'

export default {
  name: 'eventsMember',
  props: {
    event: Object,
    preview: Boolean,
    userinfo: Object,
  },

  data: function () {

    return {
      loading: false,
      pName: Object
    }

  },

  computed: mapState({
    auth: state => state.auth,
    keyword: function () {
      let pStateUsers = this.$store.state.users
      var membership = this.event?.content.membership

      if (membership === 'join') return "joined in the chat"
      if (membership === 'invite') {
        this.pName = _.findWhere(pStateUsers, {id: this.event.content.displayname})
        for (let a in pStateUsers) {
          if (a.id === this.event.content.displayname) {
          }
        }
        // console.log(this.pName, "2222222")
        return "invited " + `${this.pName.name}` + " in the chat"
      }
      if(membership === 'leave'){
        let kickedUser = this.event.content.displayname
        let name = ''
        _.mapObject(pStateUsers,function (key, value){
          if(key.id === kickedUser){
            name = key.name
          }
        })
        return 'kicked' + ' ' + name
      }

      return "Leave"

    }
  }),

  methods: {},
}